.background {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 5;
    background-color: rgba(0, 0, 0, 0.548);
}
.todoedit__insert {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 300px;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    h2 {
        box-sizing: border-box;
        width: 100%;
        margin: 0;
        text-align: center;
        padding: 0.5rem;
        background-color: #f9dce1;
    }

    input {
        margin: 2rem 0;
        padding: 0.3rem;
        border: none;
        outline: none;
        border-bottom: 1px solid #f9dce1;
    }

    button {
        cursor: pointer;
        background-color: #f9dce1;
        border: none;
        padding: 0.5rem 1rem;
        margin-bottom: 1rem;
    }
}